.navbar {
    height: 55px;
    box-shadow: 0 3px 6px var(--shadow-color);
    background-color: var(--background-color);
    color: var(--text-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 990;
    position: fixed;
    width: 100%;
    top: 0;
    .logo {
        font-size: 1.5em;
        font-weight: bolder;
        color: var(--primary-color);
    }
    .nav-links {
        a {
            margin-left: 1em;
            color: var(--text-light);
            letter-spacing: 2px;
            text-transform: uppercase;
            position: relative;
            &::before{
                content: '';
                display: inline-block;
                height: 2px;
                width: 100%;
                background-color: var(--primary-color);
                position: absolute;
                bottom: -5px;
                left: 0;
                transform: scaleX(0);
                transition: transition getAnimationDuration(.5s) ease-in-out;
            }
            &:hover{
                &::before{
                    transform: scaleX(1);
                }
            }

            &.active {
                color: var(--primary-color);
                font-weight: bold;
            }

        }
    }
    .sidebar-btn {
        height: 24px;
        width: 30px;
        position: relative;
        display: none;
        .bar {
            border-radius: 5px;
            height: 4px;
            width: 100%;
            background-color: var(--text-color);   
            position:absolute;
            transition: transform getAnimationDuration(.2s) ease-in-out;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: 50%;
                transform-origin: right;
                transform: scaleX(.8) translateY(-50%);
                
            }
            &:nth-child(3) {
                top: 100%;
                transform-origin: right;
                transform: scaleX(1.1) translateY(-100%);
            }
        }

        &.active {
            .bar {
                transform-origin: center;
                top: 50%;
                &:nth-child(1) {
                    transform: scale(1)  translate(0, -50%) rotate(45deg);
                }
                &:nth-child(2) {
                    transform: scaleX(0) translateY(-50%);
                }
                &:nth-child(3) {
                    transform: scale(1)  translate(0, -50%) rotate(-45deg);
                }
                

            }
        }
    }
}

.sidebar {
    position: fixed;
     z-index: 999;
    top:0;
    left: 0;
    height: 100vh;
    width: 300px;
    background-color: var(--background-color);
    box-shadow: 0 0 0 2000px rgba(0,0,0,0.2), 3px 0 5px var(--shadow-color);
    animation: getAnimationDuration(.5s) slideInLeft;
    .sidebar-link {
        display: block;
        padding: .5em 1em;
        border-left: .3em solid transparent;
        margin-bottom: 1em;
        color: var(--text-color);
        transform : translateX(-100%);
        animation: getAnimationDuration(.3s) slideInLeft forwards;
        @for $i from 1 through 4 {
            &:nth-child(#{$i}) {
               animation-delay: #{$i * .2}s;
            }
        }
        &.active {
            border-left-color: var(--primary-color)
        }
        &:hover {
            border-left-color: var(--primary-color);
            background-color : rgba(0,0,0,0.05)
        }
        svg {
            width: 1.2em;
            margin-right: .5em;

        }
    }
}